import React, { Component } from 'react';
import './App.css';
import './Bulma-0.6.2.min.css';
import localforage from 'localforage';

class App extends Component {
	constructor() {
		super();
		this.state = {
			currency: 'USD',
			amount: 0,
			storeName: '',
			payType: 'Credit Card',
			description: '',
			category: 'Select A Category',      
      items: [],
      totalToday: 0,
      totalMonth: 0,
      totalYTD: 0,
      filterText: ''
    };
  }


  componentDidMount() {
    this.refreshList();
  }


  refreshList = () => {
    let entries = [];

    // javascript react promises are awkward and weird
    var that = this;
  
    localforage.iterate(function(value, key, iterationNumber) {
      // Resulting key/value pair -- this callback
      // will be executed for every item in the
      // database.
      //console.log([key, JSON.stringify(value)], iterationNumber);
      //add it to the array  
      entries.push(value);
    }).then(function() {
      console.log('Iteration has completed');
      //console.log('Sorting list with date descending and redisplaying...');
      // sort by date descending
      let sortedEntries = entries.sort((a,b) => {
        let aTimestamp = (new Date(a["timestamp"])).getTime();
        let bTimestamp = (new Date(b["timestamp"])).getTime();
        return bTimestamp - aTimestamp;
      });
      //console.log(`sortedEntries = ${JSON.stringify(sortedEntries)}`);
      that.setState({items: sortedEntries});
      that.analyze();
    }).catch(function(err) {
      // This code runs if there were any errors
      console.log(err);
    });  
  }

  
  handleFormChange(attr, event) {
    console.log(attr, event.target.value);
    this.setState({
      [attr]: event.target.value
    });
  }


  handleListFilterChange = (event) =>  {
    this.refreshList();

    let inputVal = event.target.value;
    console.log(`inputVal = ${inputVal}`);
    const filteredArray = this.state.items.filter((item) => {
      let stringifiedAndLowerCasedItem = JSON.stringify(item).toLowerCase();
      console.log(stringifiedAndLowerCasedItem);
      if (stringifiedAndLowerCasedItem.indexOf(inputVal.toLowerCase()) > -1) {
        return item;
      }
    });

    if (filteredArray.length > 0) {
      var that = this;
      setTimeout(function () {
        that.setState({items: filteredArray});
        that.setState({filterText: inputVal});
        that.analyze();
      }, 1000);
    }
  } 


  handleItemClick = (item) => {
    //console.log(`Yay! you clicked an item! ${JSON.stringify(item)}`);
    this.showItemDetails(item);
    currentItem = item;
  }


  handleDeleteItemClick = (event) =>  {
    event.preventDefault();
    removeFromLocalDB(currentItem);
    this.refreshList();
    this.analyze();
  }  


  handleFormSubmit = (event) =>  {
		event.preventDefault();
    let today = new Date();

    // Get input
    let entry = {
      timestamp: today.toISOString(),
      currency: this.state.currency,
      amount: this.state.amount,
      storeName: this.state.storeName,
      payType: this.state.payType,
      description: this.state.description,
      category: this.state.category
    };

    // save entry to db
    addToLocalDB(today.getTime().toString(), entry);

    // Sort list by date descending (i.e. new ones on top)
    let updateVDOMArray = this.state.items.concat(entry).sort((a,b) => {
      let aTimestamp = (new Date(a["timestamp"])).getTime();
      let bTimestamp = (new Date(b["timestamp"])).getTime();
      return bTimestamp - aTimestamp;
    });
    this.setState({items: updateVDOMArray});

    let that = this;
    // wait a bit so that the vdom can recover
    setTimeout(function () {
      that.analyze();
    }, 1000);
  }   


  showItemDetails(item) {
    let itemId = new Date( item["timestamp"] ).getTime().toString();

    localforage.getItem(itemId).then(function(value) {
      // This code runs once the value has been loaded
      // from the offline store.
      //console.log(value);
      let amount = Number(value["amount"]).toFixed(2);
      let currency = value["currency"];
      let storeName = value["storeName"];
      let payType = value["payType"];
      let desc = value["description"];
      let category = value["category"];
      document.getElementById("currentItemAmt").value = amount;
      document.getElementById("currentItemCurrency").value = currency;
      document.getElementById("currentItemStoreName").value = storeName;
      document.getElementById("currentItemPayType").value = payType;    
      document.getElementById("currentItemDesc").value = desc;
      document.getElementById("currentItemCategory").value = category
  
      let itemSummary = `Spent ${amount} ${currency} on ${payType} at ${storeName} for ${desc} - ${category}`;
  
      document.getElementById("currentItemSummary").innerHTML = itemSummary;

      currentItem = itemId;
    
      document.getElementById("item-modal").classList.add("is-active");      
    }).catch(function(err) {
      // This code runs if there were any errors
      console.log(err);
    });  
  }  
  
  
  analyze() {
    let totalToday = 0;
    let totalMonth = 0;
    let totalYTD = 0;

    let i=0;
    const iMax = this.state.items.length;
    
    for (; i<iMax; i++) {
      let item = this.state.items[i];
      let today = (new Date()).toISOString();
      let currentItemDate = (new Date(item["timestamp"])).toISOString();
  
      let amount = Number(item["amount"]);
      if (today.slice(0,10) === currentItemDate.slice(0,10)) {
        totalToday += amount;
      }
      
      if (today.slice(0,7) === currentItemDate.slice(0,7)) {
        totalMonth += amount;
      }
  
      if (today.slice(0,4) === currentItemDate.slice(0,4))  {
        totalYTD += amount;
      }       
    }

    this.setState({totalToday: totalToday});
    this.setState({totalMonth: totalMonth});
    this.setState({totalYTD: totalYTD});
  }
  

  render() {
    return (
      <div className="App">
        <section className="section">
          <div className="content">
            <h3 className="has-text-centered">
              Track Spending
            </h3>
          </div>
          <div className="tabs is-centered">
            <ul>
              <li id="enter-data-list-item" className="is-active">
                <a onClick={(e) => showTab('enter-data', e)}>
                  Enter
                </a>
              </li>
              <li id="list-entries-list-item">
                <a onClick={(e) => showTab('list-entries', e)}>
                  List
                </a>
              </li>
              <li id="analyze-list-item">
                <a onClick={(e) => showTab('analyze', e)}>
                  Analyze
                </a>
              </li>
            </ul>
          </div>
          <div id="enter-data" className="column">      
            <form onSubmit={this.handleFormSubmit}>
              <div className="columns is-gapless">
                <div className="column" />
                <div className="column">
                  <label className="label">Amount</label>
                  <div className="field has-addons">
                    <p className="control">
                      <span className="select">
                        <select 
                          value={this.state.currency} 
                          onChange={this.handleFormChange.bind(this, "currency")}>
                          <option value="USD">$</option>
                          <option value="GBP"></option>
                          <option value="EUR"></option>
                        </select>
                      </span>
                    </p>
                    <p className="control is-expanded">
                      <input 
                        className="input" 
                        type="number" 
                        placeholder="How much did you pay?" 
                        step="any"
                        min="0"
                        defaultValue="{this.state.amount}" 
                        onChange={this.handleFormChange.bind(this, "amount")}/>
                    </p>
                  </div>{/* end: amount field */}
                  <div className="field">
                    <label className="label">Store Name</label>
                    <div className="control has-icons-left">
                      <input 
                        className="input" 
                        type="text" 
                        placeholder="Where did you spend this money?"
                        defaultValue="" 
                        onChange={this.handleFormChange.bind(this, "storeName")} />
                      <span className="icon is-small is-left">
                        <i className="fas fa-university"></i>
                      </span>
                    </div>
                  </div>{/* end: store name field */}
                  <div className="field">
                    <label className="label">Payment Type</label>
                    <div className="control is-expanded">
                      <div className="select">
                        <select 
                          value={this.state.payType} 
                          onChange={this.handleFormChange.bind(this, "payType")}>
                          <option value="Credit Card">Credit Card</option>
                          <option value="Debit Card">Debit Card</option>
                          <option value="EC Card">EC Card</option>
                          <option value="Cash">Cash</option>
                        </select>
                      </div>
                    </div>
                  </div>{/* end: pay type field */}            
                  <div className="field">
                    <label className="label">Description</label>
                    <div className="control has-icons-left">
                      <input  
                        className="input" 
                        type="text" 
                        placeholder="Any additional details?" 
                        defaultValue="" 
                        onChange={this.handleFormChange.bind(this, "description")} />
                      <span className="icon is-small is-left">
                        <i className="fas fa-asterisk"></i>
                      </span>
                    </div>
                  </div>{/* end: description field */}
                  <div className="field">
                    <label className="label">Category</label>
                    <div className="control is-expanded">
                      <div className="select">
                        <select 
                          value={this.state.category} 
                          onChange={this.handleFormChange.bind(this, "category")} >
                          <option value="">Select A Category</option>
                          <option value="Entertainment">Entertainment</option>
                          <option value="Gifts">Gifts</option>
                          <option value="Groceries">Groceries</option>
                          <option value="Health Care">Health Care</option>
                          <option value="Maintenance">Maintenance</option>
                          <option value="Rent">Rent</option>
                          <option value="Restaurants">Restaurants</option>
                          <option value="Supplies">Supplies</option>
                          <option value="Training">Training</option>
                          <option value="Travel">Travel</option>
                          <option value="Utilities">Utilities</option>
                        </select>
                      </div>
                    </div>
                  </div>{/* end: category field */}

                  <div className="field is-grouped">
                    <p className="control">
                      <a type="submit" 
                        onClick={this.handleFormSubmit} 
                        className="button is-success">
                        Add
                      </a>
                    </p>
                    <p className="control">
                      <a className="button is-danger">
                        Clear
                      </a>
                    </p>
                  </div>            
                
                </div>
                <div className="column" />
              </div>{/* end: enter-data */}
            </form>
          </div>
          <div id="list-entries" className="column" style={{display: "none"}}>
            <input 
              className="input" 
              type="text" 
              placeholder="Start typing here to filter"
              defaultValue="" 
              onChange={this.handleListFilterChange} />      
            <table id="entry-items" className="table">
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Merchant</th>
                  <th>Amount</th>
                </tr>
              </thead>
              <tbody>
                {
                  this.state.items && this.state.items.map((item) =>             
                    <tr key={new Date(item["timestamp"]).getTime()} 
                      onClick={(e) => this.handleItemClick(item, e)}>
                      <td>{ (new Date(item["timestamp"])).toLocaleString() }</td>
                      <td>{ item["storeName"] }</td>
                      <td className="has-text-right">{ Number(item["amount"]).toFixed(2) }</td>
                    </tr>                
                  )
                }          
              </tbody>
            </table>
            <button onClick={downloadCurrentSavedData} className="button is-info">
              Download Data
            </button>            
          </div>
          <div id="analyze" className="column" style={{display: "none"}}>
            Filter text: {this.state.filterText}
            <table id="analysis-items" className="table">
              <tbody>
                <tr>
                  <td>total (today):</td>
                  <td className="has-text-right">
                    { Number(this.state.totalToday).toFixed(2) }
                  </td>
                </tr>
                <tr>
                  <td>total (month):</td>
                  <td className="has-text-right">
                    { Number(this.state.totalMonth).toFixed(2) }
                  </td>
                </tr>
                <tr>
                  <td>total (year-to-date):</td>
                  <td className="has-text-right">
                    { Number(this.state.totalYTD).toFixed(2) }
                  </td>
                </tr>                 
              </tbody>
            </table>              
          </div>               
        </section>

        <div id="item-modal" className="modal">
          <div className="modal-background"></div>
          <div className="modal-card">
            <header className="modal-card-head">
              <p className="modal-card-title">Item Details</p>
              <button 
                className="delete" 
                aria-label="close" 
                onClick={closeItemDetailsModal}></button>
            </header>      
            <section className="modal-card-body">
              <table className="table">
                <tbody>
                  <tr>
                    <td>Amount:</td>
                    <td><input id="currentItemAmt" type="number" /></td>
                  </tr>
                  <tr>
                    <td>Currency:</td>
                    <td><input id="currentItemCurrency" type="text" /></td>
                  </tr>
                  <tr>
                    <td>Store Name:</td>
                    <td><input id="currentItemStoreName" type="text" /></td>
                  </tr>
                  <tr>
                    <td>Payment Type:</td>
                    <td><input id="currentItemPayType" type="text" /></td>
                  </tr>            
                  <tr>
                    <td>Description:</td>
                    <td><input id="currentItemDesc" type="text" /></td>
                  </tr>
                  <tr>
                    <td>Category:</td>
                    <td><input id="currentItemCategory" type="text" /></td>
                  </tr>                                    
                </tbody>
              </table>
              <div id="currentItemSummary"></div>
            </section>
            <footer className="modal-card-foot">
              <button className="button is-success">Save</button>
              <button onClick={this.handleDeleteItemClick} className="button is-danger">
                Delete
              </button>
              <button onClick={shareItem} className="button is-info">
                Share
              </button>
            </footer>
          </div>
        </div>{/*<!-- end: item-modal --> */}       
      </div>
    );
  }
}

// Sometimes plain old javascript makes everything better
// So put that stuff here

var currentItem = "";


// Set up localforage to use IndexedDB
localforage.config({
  driver      : localforage.INDEXEDDB, // Force INDEXEDDB; same as using setDriver()
  name        : 'track-spending',
  version     : 1.0,
  size        : 4980736, // Size of database, in bytes. WebSQL-only for now.
  storeName   : 'track_spending', // Should be alphanumeric, with underscores.
  description : 'track spending'
}); 


function addToLocalDB(itemId, entry) {
  console.log(`adding ${JSON.stringify(entry)}`);

  // insert data into local DB
  localforage.setItem(itemId, entry).then(function () {
    return localforage.getItem('key');
  }).then(function (value) {
    // we got our value
    showTab('list-entries');
  }).catch(function (err) {
    // we got an error
    alert("adding item failed");
  });
}


function removeFromLocalDB(itemId) {
  localforage.removeItem(itemId).then(function() {
    // Run this code once the key has been removed.
    console.log(`${JSON.stringify(itemId)} deleted!`);
    closeItemDetailsModal();
  }).catch(function(err) {
      // This code runs if there were any errors
      console.log(err);
  });
}


function showTab( tabId ) {
  if (tabId === "enter-data") {
    document.getElementById( "enter-data" ).style.display = "block";
    document.getElementById( "list-entries" ).style.display = "none";
    document.getElementById( "analyze" ).style.display = "none";
    document.getElementById( "enter-data-list-item" ).classList.add("is-active");
    document.getElementById( "list-entries-list-item" ).classList.remove("is-active");
    document.getElementById( "analyze-list-item" ).classList.remove("is-active");
  }
  else if (tabId === "list-entries") {
    document.getElementById( "enter-data" ).style.display = "none";
    document.getElementById( "list-entries" ).style.display = "block";
    document.getElementById( "analyze" ).style.display = "none";
    document.getElementById( "enter-data-list-item" ).classList.remove("is-active");
    document.getElementById( "list-entries-list-item" ).classList.add("is-active");
    document.getElementById( "analyze-list-item" ).classList.remove("is-active");
  }
  else if (tabId === "analyze") {
    document.getElementById( "enter-data" ).style.display = "none";
    document.getElementById( "list-entries" ).style.display = "none";
    document.getElementById( "analyze" ).style.display = "block";
    document.getElementById( "enter-data-list-item" ).classList.remove("is-active");
    document.getElementById( "list-entries-list-item" ).classList.remove("is-active");
    document.getElementById( "analyze-list-item" ).classList.add("is-active");
  }
  document.getElementById( tabId ).style.display = "block";
}


function closeItemDetailsModal() {
  currentItem = "";
  document.getElementById("item-modal").classList.remove("is-active");
}


function shareItem() {
  console.log( "Share button clicked! Let's see if anything else happens." );

  let itemTextToShare = document.getElementById("currentItemSummary").innerHTML ||
    "Sorry, your browser and/or phone doesn't allow sharing.";

  if (navigator.share) {
    console.log( `Share is allowed. Yay! Will now try to share ${itemTextToShare}` );
    navigator.share({
        title: 'Track Spending Item',
        text: itemTextToShare
    }).then(function() {
      console.log('Successful share');
    }).catch(function(err) {
      // This code runs if there were any errors
      alert('Error sharing', err);
    });
  } 
  else {
    alert("Sorry, this only works on Android Chrome.")
  }  
}


function downloadCurrentSavedData() {

  console.log( "Export button clicked! Let's see if anything else happens." );

  let entries = [];

  localforage.iterate(function(value, key, iterationNumber) {
    // Resulting key/value pair -- this callback
    // will be executed for every item in the
    // database.
    //console.log([key, JSON.stringify(value)], iterationNumber);
    //add it to the array  
    entries.push(value);
  }).then(function() {
    console.log('Iteration has completed');
    let strEntries = JSON.stringify(entries);
    console.log(`downloading as json = ${strEntries}`);

    // create download link from indexeddb entries
    let dataStr = "data:text/json;charset=utf-8," + 
      encodeURIComponent(strEntries);
    let downloadAnchorNode = document.createElement('a');
    downloadAnchorNode.setAttribute("href",     dataStr);
    downloadAnchorNode.setAttribute("download", 'track-spending-backup.json');

    // download the data to your local
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  }).catch(function(err) {
    // This code runs if there were any errors
    console.log(err);
  });   
}


function exportCurrentSavedData() {
  console.log( "Export button clicked! Let's see if anything else happens." );

  let entries = [];

  localforage.iterate(function(value, key, iterationNumber) {
    // Resulting key/value pair -- this callback
    // will be executed for every item in the
    // database.
    //console.log([key, JSON.stringify(value)], iterationNumber);
    //add it to the array  
    entries.push(value);
  }).then(function() {
    console.log('Iteration has completed');
    let strEntries = JSON.stringify(entries);
    alert(`entries = ${strEntries}`);

    if (navigator.share) {
      console.log( "Share is allowed. Yay!" );
      
      navigator.share({
          title: 'Track Spending Backup Files',
          text: strEntries
      }).then(function() {
        console.log('Successful share');
      }).catch(function(err) {
        // This code runs if there were any errors
        alert('Error sharing', err);
      });
    } 
    else {
      alert("Sorry, this only works on Android Chrome.")
    }
  }).catch(function(err) {
    // This code runs if there were any errors
    console.log(err);
  });   
}


export default App;


